@import "../mixin.scss";
.dropdown {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  z-index: 1;
  max-width: 320px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  // padding: 24px;
  background: #2e3031;
  border-radius: 5px;
  visibility: hidden;
  opacity: 0;
  transition: 0.5s;
}

.dropdown_open {
  visibility: visible;
  opacity: 1;
}

.dropdown__close {
  border: none;
  background-color: transparent;
  padding: calc(1.5rem + 12px) 1.5rem 1.5rem 1.5rem;
  cursor: pointer;
  align-self: flex-end;
  transition: 0.3s;
}
.dropdown__close:hover {
  background-color: $blue-color;
  border-radius: 3px;
}
.dropdown__nav-list {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.dropdown__nav {
  width: 100%;
}
.dropdown__nav-item {
  width: 100%;
  transition: 0.3s;
}
.dropdown__nav-item:hover {
  background-color: $blue-color;
}

.dropdown__link {
  display: flex;
  // justify-content: center;
  gap: 12px;
  padding: 1.2rem 1.2rem 1.2rem 70px;
}
.dropdown__link-txt {
  @include regularText;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 2px;
  cursor: pointer;
}
